<template >

    <svg class="svg-icon" v-if="type === 'svg'">
      <use :xlink:href="`#${iconClass}`">
        <animateTransform v-if="animate" attributeType="xml" attributeName="transform" type="rotate" 
        :from="animateRangeEnd" :to="animateRangeFrom" dur="0.8s" repeatCount="indefinite">
        </animateTransform>
      </use>
    </svg>
</template>
<script>
export default {
  name: 'PicIcon',
  props: {
    iconClass: {
      type: String,
      required: true
    },
    type: {
      type: String,
      default: 'svg'
    },
    className: {
      type: String,
      default: ''
    },
    animate:{
      type:Boolean,
      default:false,
    }
  },
  data() {
    return {
    };
  },
  created(){
  },
  computed: {
    animateRangeEnd(){
        return `0 ${this.animateHeightCircle} ${this.animateHeightCircle}`
    },
    animateRangeFrom () {
        return `360 ${this.animateHeightCircle} ${this.animateHeightCircle}`
    },

  },
  watch:{
  },
}
</script>
<style lang="scss" scoped>

.svg-icon {
  fill: currentColor;
  overflow: hidden;
}

</style>
